@font-face {
  src: local("Inter Regular"), local("Inter-Regular"), url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  src: local("Inter Bold"), local("Inter-Bold"), url("https://fonts.googleapis.com/css2?family=Inter:wght@700&display=swap");
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

/*img
 * max-width: 100%*/
.container {
  margin: auto;
  max-width: 1140px;
  padding: 0 30px;
}

@media (max-width: 480px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.body {
  font-family: "Inter", "Arial", sans-serif;
  background-color: #D7FC52;
}

.main {
  margin: auto;
  position: relative;
  overflow: hidden;
}
.main__welcome-text {
  padding-top: 60px;
}
.main__food {
  padding-top: 60px;
}
.main__rent-home {
  padding-top: 60px;
}

.btn {
  font-size: 18px;
  color: #D7FC52;
  background-color: #000100;
  padding: 0 30px;
  height: 50px;
  border-radius: 100px;
  border: 2px solid #000100;
  text-decoration: none;
}
.btn:hover {
  transition: all 0.3s ease-in-out;
  color: #FFFFFF;
  background-color: #FC53C4;
  border: 2px solid #FC53C4;
}

.btn__navigation {
  font-size: 18px;
  color: #D7FC52;
  background-color: #000100;
  padding: 0 30px;
  height: 50px;
  margin: auto;
  border-radius: 100px;
  border: 2px solid #000100;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn__navigation:hover {
  transition: all 0.3s ease-in-out;
  color: #FFFFFF;
  background-color: #FC53C4;
  border: 2px solid #FC53C4;
}
.btn__navigation:active {
  transform: translateY(3px);
}

.btn__accent {
  background-color: #FC53C4;
  color: #FFFFFF;
  border: none;
}
.btn__accent:hover {
  transition: all 0.3s ease-in-out;
  background-color: #FF7BD3;
  border: none;
}
.btn__accent:hover {
  transition: all 0.3s ease-in-out;
  background-color: #CD379B;
  border: none;
}

.btn__item {
  font-weight: bold;
}

@media (max-width: 480px) {
  .btn {
    padding: 17px 27px;
    font-size: 18px;
  }
}
.navigation__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #D7FC52;
  flex-wrap: wrap;
  margin-top: 10px;
}
.navigation__btn {
  margin-right: 10px;
  margin-bottom: 10px;
}

.welcome-text {
  text-align: center;
}
.welcome-text--content {
  font-size: 24px;
  color: #000100;
  padding: 0 30px;
  font-weight: 400;
  line-height: 29px;
  text-align: center;
}

.food__heading {
  font-size: 42px;
  text-align: center;
  color: #000100;
  line-height: 1.25;
  font-weight: bold;
}
.food__description {
  font-size: 18px;
  text-align: center;
  color: #000100;
  line-height: 1.25;
  margin-top: 15px;
}
.food__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px 30px;
  grid-template-columns: auto auto auto;
  margin-top: 30px;
  justify-content: center;
  align-content: center;
}

.rent-home__heading {
  font-size: 42px;
  text-align: center;
  color: #000100;
  line-height: 1.25;
  font-weight: bold;
}
.rent-home__description {
  font-size: 18px;
  text-align: center;
  color: #000100;
  line-height: 1.25;
  margin-top: 15px;
}
.rent-home__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  grid-template-columns: auto auto auto;
  margin-top: 30px;
  justify-content: center;
}

.item {
  text-align: center;
  line-height: 1.25;
  max-width: 290px;
}
.item__img {
  width: 290px;
  height: 290px;
  border-radius: 15px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.35);
  object-fit: cover;
}
.item__heading {
  font-size: 20px;
  font-weight: bold;
  margin-top: 15px;
}
.item__price {
  font-size: 16px;
  margin-top: 10px;
}
.item__buy {
  margin-top: 15px;
}
.item__img--wrapper {
  max-width: 290px;
  max-height: 290px;
  overflow: hidden;
}